.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
}
.products{
  justify-content: space-between;
  margin-left: 120px;
  margin-right: 120px;
  float: left;
}
h2{
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
}
h3{
  font-family: Arial, Helvetica, sans-serif;
}
.block{
  background-color: palegreen;
  padding: 1rem;
  margin: 0.5rem;
  border-radius: 0.5rem;
}
.ProductPic{
  width: 150px;
}
.add{
  font-size: 0.8rem;
  padding: 0.2rem;
  margin: 0.1rem;
  border-radius: 0.1rem #404040 solid;
  background-color: whitesmoke;
  cursor: pointer;
  width: 100%;
}
.ostoskori{
  justify-content: space-between;
  float: right;
  margin-right: 17%;
}
.form{
  text-align: center;
}
.formbutton{
  font-size: 0.8rem;
  padding: 0.2rem;
  margin: 0.1rem;
  border-radius: 0.1rem #404040 solid;
  background-color: whitesmoke;
  cursor: pointer;
}
.ostoskoriButton{
  font-size: 0.8rem;
  padding: 0.2rem;
  margin: 0.1rem;
  border-radius: 0.1rem #404040 solid;
  background-color: whitesmoke;
  cursor: pointer;
}
p{
  text-align: center;
}